<template>
  <button class="uc-button" :type="type" :class="{ primary }" @click="clicked">{{text}}</button>
</template>

<script>
export default {
  name: 'uc-button',
  props: {
    text: String,
    disabled: Boolean,
    primary: Boolean,
    type: String
  },
  methods: {
    clicked (e) {
      this.$emit('click', e)
    }
  }
}
</script>

<style lang="stylus">
  .uc-button
    border none
    outline none
    background-color #fff
    box-shadow 1px 2px 6px rgba(0,0,0,0.25)
    border-radius 4px
    padding 8px 30px
    font-size 0.75rem
    cursor pointer
    transition background-color 150ms ease
    &:hover
      background-color rgb(240, 240, 240)
    &.primary
      background-color $col_primary
      color #fff
      &:hover
        background-color lighten($col_primary, 8)
    &:active
      box-shadow none
      transform scale(0.95)

</style>
