<template>
  <div class="uc-image" :style="{width: size, height: size}" :class="{cover, readonly}" @click="clickHandler">
    <img v-if="!fallback && src" :class="{cover}" :src="src" :alt="alt" @error="showDefault">
    <camera-icon v-if="fallback" />
    <folder-icon v-if="!src && !fallback" class="folder"/>
  </div>
</template>

<script>
import CameraIcon from '../icons/camera'
import FolderIcon from '../icons/folder-image'

export default {
  name: 'uc-image',
  data () {
    return {
      fallback: false
    }
  },
  props: {
    src: String,
    alt: String,
    cover: Boolean,
    size: { type: String, default: 'auto' },
    readonly: Boolean
  },
  components: {
    CameraIcon,
    FolderIcon
  },
  methods: {
    showDefault () {
      this.fallback = true
    },
    clickHandler () {
      this.$emit('click')
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-image
  center()
  cursor pointer
  padding 8px
  background-color rgba(112, 135, 144, .08)
  &.readonly
    cursor default
    pointer-events none
  img
    max-width 100%
    max-height 100%
    width auto
    height auto
  svg
    box(45%)
    display block
    margin auto
    opacity .4
</style>
