<template>
  <div>
    <div v-if="definition">
      <uc-label :definition="definition" :language="language"></uc-label>
      <uc-image v-if="definition" :src="definition.mediaUrl" :alt="definition.mediaName" :size="'256px'" @click="toggle()"/>
      <label>{{ selectedItemsSummary }}</label>
      <div class="actions">
        <div class="icon small background" @click.prevent.stop="toggle()"><edit-icon class="icon-edit"/></div>
        <div class="icon small background" @click.prevent.stop="reset()"><close-icon class="icon-close align-right"/></div>
      </div>
    </div>
    <div v-if="toggled" class="picker dialog">
      <header class="picker header-section">
        <div class="picker header-label">{{ definition.label }}</div>
        <div class="icon big align-right" @click="toggled = !toggled"><close-icon class="icon-close"/></div>
      </header>
      <content-navigator class="padding-x" :dataUrl="contentType"/>
      <content-selector class="picker selector padding-x" :multiSelect="multiSelect" :preSelectedItems="definition.selected"/>
      <preview-bar class="picker preview-bar" @save="saveAndClose"/>
    </div>
  </div>
</template>

<script>
import UcImage from '../../../shared/components/uc-image'
import UcLabel from '../../../shared/components/forms/uc-label'
import CloseIcon from '../../icons/close'
import EditIcon from '../../icons/edit-pencil'
import ContentNavigator from '../../../CatalogFoundation/components/content-navigator/content-navigator'
import ContentSelector from '../../../CatalogFoundation/components/content-navigator/content-selector'
import PreviewBar from '../../../CatalogFoundation/components/content-navigator/content-selector-preview-bar'
import createSaveCommand from '../../../shared/js/create-save-command'

export default {
  name: 'uc-image-picker',
  components: {
    UcImage,
    CloseIcon,
    EditIcon,
    ContentNavigator,
    ContentSelector,
    PreviewBar,
    UcLabel
  },
  props: {
    name: String,
    definition: Object,
    multiSelect: Boolean,
    contentType: String,
    language: String
  },
  data () {
    return {
      toggled: false,
      lastScrollPosition: Number
    }
  },
  computed: {
    selectedItemsSummary () {
      const count = (this.definition.selected || []).length
      switch (count) {
        case 0: return 'Nothing selected'
        case 1: return this.definition.mediaName
        default: return `${this.definition.mediaName} and ${count - 1} others selected`
      }
    }
  },
  watch: {
    toggled () {
      if (this.toggled) {
        this.lastScrollPosition = document.documentElement.scrollTop
        document.documentElement.style.overflow = 'hidden'
        document.addEventListener('keydown', this.keydownHandler)
      } else {
        document.documentElement.style.overflow = 'auto'
        document.removeEventListener('keydown', this.keydownHandler)
        document.documentElement.scrollTop = this.lastScrollPosition
      }
    }
  },
  methods: {
    toggle () {
      this.toggled = !this.toggled
    },

    reset () {
      this.save([])
    },

    async save (guids) {
      const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, guids, (this.definition.selected || []), 'selected')
      if (guids.length) {
        const content = await this.axios.get(`${this.contentType}/${guids[0]}`)
        let { save } = saveCommand
        save[this.name].mediaName = content.data.name
        save[this.name].mediaUrl = content.data.url
        let { undo } = saveCommand
        undo[this.name].mediaName = this.definition.mediaName
        undo[this.name].mediaUrl = this.definition.mediaUrl
      }
      this.$emit('change', saveCommand)
    },

    saveAndClose (arr) {
      this.save(arr)
      this.toggle()
    },

    keydownHandler (e) {
      if (e.key === 'Escape') {
        this.toggle()
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
$padding = 8px

.actions
  display flex
  padding $padding
  width fit-content
  position relative
  top -76px
  right -156px

$sizeSmall = 16px
$sizeBig = $sizeSmall * 2
.icon
  center()
  padding $padding
  margin 4px
  cursor pointer
  opacity 0.8
  &.small
    circle($sizeSmall)
    box($sizeBig)
  &.big
    circle($sizeBig)
    box($sizeBig * 2)
  &.background
    background-color $col_light_border
  &:hover
    background-color $col_light_gray

.icon-edit
  box($sizeBig)
.icon-close
  box($sizeBig * 2)

.picker
  &.dialog
    display flex
    flex-direction column
    position absolute
    top 0
    left 0
    z-index 1000
    width 100%
    height 100%
    background-color #fff
  &.header-section
    display flex
    justify-content space-between
    align-items center
    padding 20px
    padding-bottom 0
  &.header-label
    font-size 1.5em
    truncate()

  &.selector
    margin 32px 0
  &.preview-bar
    margin-top auto

.padding-x
  padding 0 20px
.align-right
  margin-left auto
</style>
