<template>
  <div>
    <store-selector/>
    <section ref="builder"
    class="cat-builder"
    @click="setFocus"
    @keydown="keydownHandler"
    @keydown.esc="hideCreate"
    @keyup.ctrl.alt.right.left="moveCreateWidget">
      <div ref="inner" class="cat-builder__inner" :style="{transform: xPosition}">
        <div class="cat-builder__header"></div>
        <tree-section v-for="(section, index) in treeSections" ref="section" :key="index" :section="section" @nodeSelected="nodeSelected" @nodeDropped="nodeDropped" @nodeCreated="nodeCreated"  @needsMoreCats="loadMoreCats"></tree-section>
      </div>
      <transition name="fade">
        <div v-show="showScroll" class="builder__scrollbar">
          <div class="builder__scroll-handle" :class="{scrolling: scrolling}" ref="scroller" :style="{width: `${scrollerWidthPercent}%`, transform: `translate3d(${scrollerPosition}px, 0, 0)`}"></div>
        </div>
      </transition>
    </section>
  </div>
</template>

<script>
import StoreSelector from './store-selector.vue'
import TreeSection from './tree-section.vue'
import MapCats from '../../js/map-category-response'
import { nodeTypeApiMap, catalog } from '../../js/tree-node-types'
import { createNamespacedHelpers, mapGetters, mapMutations, mapActions } from 'vuex'
const builderStore = createNamespacedHelpers('builder')
export default {
  name: 'catalog-builder',
  data () {
    return {
      builderWidth: 0,
      visibleSections: 0,
      slidePosition: 0,
      sectionClicked: 0,
      scrolling: false,
      scrollInit: {
        mouseX: 0,
        scrollerPos: 0,
        slidePos: 0
      },
      scrollMove: 0,
      isLoadingMoreItems: false
    }
  },
  computed: {
    ...builderStore.mapGetters(['treeSections', 'displayedNodePosition', 'sectionWidth', 'displayedNode', 'dragInfo', 'selectedStore']),
    ...mapGetters(['contentLanguage']),
    xPosition () {
      return `translate3d(-${this.slidePosition * this.sectionWidth}px, 0, 0)`
    },
    sectionsInTree () {
      return this.treeSections.length
    },
    showScroll () {
      return (this.sectionsInTree * this.sectionWidth) > (this.builderWidth + 50)
    },
    scrollerWidthPercent () {
      return (this.builderWidth / (this.sectionsInTree * this.sectionWidth)) * 100
    },
    scrollerWhiteSpace () {
      return this.builderWidth - (this.scrollerWidthPercent * this.builderWidth) / 100 // remaining horizontal space
    },
    scrollerPageSize () {
      return this.scrollerWhiteSpace / (this.sectionsInTree - this.visibleSections)
    },
    scrollerPosition () {
      if (!this.scrolling) {
        const xPos = this.scrollerPageSize * this.slidePosition
        return xPos
      }
      return this.scrollMove
    }
  },
  watch: {
    selectedStore () {
      this.getCatalogs()
    }
  },
  methods: {
    ...builderStore.mapMutations(['setSectionWidth', 'setSectionsInTree', 'addItemsToSectionInTree',
      'selectNode', 'resetDisplayedNode', 'setRecentDrop', 'moveDraggedNode']),
    ...mapMutations(['addUndo', 'clearUndos']),
    ...mapActions(['moveCat']),
    async loadMoreCats (cat) {
      if (cat.endOfList) return
      if (this.isLoadingMoreItems) return
      this.isLoadingMoreItems = true
      try {
        if (cat.position === 0) {
          await this.getMoreCatalogs(cat)
        } else {
          await this.getMoreCategories(cat)
        }
      } finally {
        this.isLoadingMoreItems = false
      }
      const section = this.$refs.section[cat.position]
      if (section) {
        section.checkIfMoreItemsNeeded.bind(section)()
      }
    },

    async getCatalogs () {
      const pageSize = 25
      const result = await this.axios.get(`/stores/${this.selectedStore}/catalogs?limit=${pageSize}`)
      const catalogs = MapCats(result.data)
      const sectionData = {
        position: 0,
        cat: { name: 'Catalogs', nodeType: 'store', guid: this.selectedStore },
        elements: catalogs,
        categoriesCount: +result.headers['x-total-count']
      }
      this.setSectionsInTree(sectionData)
      if (catalogs.length) {
        await this.selectNodeWithPosition(0, 0)
      } else {
        this.resetDisplayedNode()
      }
      this.resetScroll(1)
    },

    async getMoreCatalogs (cat, marker) {
      const pageSize = 100
      marker = marker || cat.categories[cat.categories.length - 1].guid
      const result = await this.axios.get(`/stores/${this.selectedStore}/catalogs?limit=${pageSize}&marker=${marker}`)
      const catalogs = MapCats(result.data)
      const sectionData = {
        position: 0,
        cat: cat,
        elements: catalogs,
        categoriesCount: +result.headers['x-total-count']
      }
      this.addItemsToSectionInTree(sectionData)
    },

    async selectParent () {
      if (this.displayedNode.nodeType === catalog) {
        await this.getCatalogs()
        return
      }
      const parentSectionIndex = this.displayedNodePosition.section - 1
      const parentIndex = this.treeSections[parentSectionIndex].categories.findIndex(x => x.selected)
      await this.selectNodeWithPosition(parentSectionIndex, parentIndex)
    },

    setWidth () {
      this.builderWidth = this.$refs.builder.offsetWidth
      this.visibleSections = Math.round(this.builderWidth / 300) // 300 being a sensible preferred width
      this.setSectionWidth(Math.ceil(this.builderWidth / this.visibleSections))
    },

    async nodeSelected ({ cat, parentGuid }) {
      const changed = cat.guid !== this.displayedNode.guid
      const sectionIndex = this.treeSections.findIndex(x => x.guid === parentGuid)
      this.selectNode({
        parentGuid,
        catGuid: cat.guid
      })
      await this.getCategories(cat, sectionIndex)
      this.sectionClicked = sectionIndex
      if (changed) {
        this.nodeChanged(sectionIndex)
      }
    },

    nodeChanged (sectionIndex) {
      this.clearUndos()
      this.resetScroll(sectionIndex + 1)
    },

    createMoveCommand (nodeInfo, sectionIndex, droppedNodeGuid, onlySort, scrollTop) {
      const selectedCatalog = this.treeSections[0].categories.find(x => x.selected)
      const _catalogGuid = nodeInfo.catalogGuid || selectedCatalog.guid

      const body = {
        sortOrder: nodeInfo.sortOrder,
        parentCategoryGuid: nodeInfo.parentGuid === _catalogGuid ? null : nodeInfo.parentGuid,
        productCatalogGuid: _catalogGuid
      }

      const undoBody = {
        sortOrder: this.dragInfo.node.sortOrder,
        parentCategoryGuid: this.dragInfo.parentGuid === selectedCatalog.guid ? null : this.dragInfo.parentGuid,
        productCatalogGuid: selectedCatalog.guid
      }
      const message = onlySort ? `<strong>${this.dragInfo.node.name}</strong> changed position` : `<strong>${this.dragInfo.node.name}</strong> was moved into <strong>${nodeInfo.name}</strong>`
      const meta = {
        ...this.dragInfo.node,
        undoMessage: message,
        nodeGuid: this.dragInfo.node.guid,
        contentLanguage: this.contentLanguage,
        survivesNodeChange: true,
        undoAction: async function () {
          await this.selectParent()
          const guidToLoad = onlySort ? droppedNodeGuid : nodeInfo.parentGuid
          this.selectNodeWithGuid(guidToLoad)
          this.setFocus()
          this.setSectionScrollPosition(sectionIndex, scrollTop)
        }.bind(this)
      }

      return { save: body, undo: undoBody, meta: meta }
    },

    async nodeDropped (nodeInfo) {
      const droppedNodeGuid = this.dragInfo.node.guid
      const onlySort = this.dragInfo.parentGuid === nodeInfo.parentGuid

      const sectionIndex = nodeInfo.parentGuid === nodeInfo.catalogGuid ? 0 : this.treeSections.findIndex(x => x.guid === nodeInfo.parentGuid)
      const scrollTop = this.getSectionScrollPosition(sectionIndex)

      const command = this.createMoveCommand(nodeInfo, sectionIndex, droppedNodeGuid, onlySort, scrollTop)
      await this.moveCat(command)

      this.moveDraggedNode(nodeInfo)
      if (nodeInfo.parentGuid === nodeInfo.catalogGuid) {
        await this.getCatalogs()
      }

      const guidToLoad = onlySort ? droppedNodeGuid : nodeInfo.parentGuid
      await this.selectNodeWithGuid(guidToLoad)
      this.setFocus()
      this.setSectionScrollPosition(sectionIndex, scrollTop)
    },

    getSectionScrollPosition (sectionIndex) {
      if (this.$refs.section[sectionIndex]) {
        return this.$refs.section[sectionIndex].getScrollPosition()
      } else {
        return 0
      }
    },

    setSectionScrollPosition (sectionIndex, scrollTop) {
      if (this.$refs.section[sectionIndex]) {
        return this.$refs.section[sectionIndex].setScrollPosition(scrollTop)
      }
    },

    async getCategories (cat, sectionIndex) {
      const api = nodeTypeApiMap[cat.nodeType]
      const pageSize = 25
      const result = await this.axios.get(`/${api}/${cat.guid}/categories?limit=${pageSize}`)
      const children = MapCats(result.data)
      const sectionData = {
        position: sectionIndex + 1,
        elements: children,
        cat: cat,
        categoriesCount: +result.headers['x-total-count']
      }
      this.setSectionsInTree(sectionData)
      this.setWidth()
      this.slideSections()
    },

    async getMoreCategories (cat, marker) {
      const api = nodeTypeApiMap[cat.nodeType]
      marker = marker || cat.categories[cat.categories.length - 1].guid
      const pageSize = 100
      const result = await this.axios.get(`/${api}/${cat.guid}/categories?limit=${pageSize}&marker=${marker}`)
      const children = MapCats(result.data)
      const sectionData = {
        position: cat.position,
        elements: children,
        cat: cat,
        categoriesCount: +result.headers['x-total-count']
      }
      this.addItemsToSectionInTree(sectionData)
      this.setWidth()
      this.slideSections()
    },

    resetScroll (sectionIndex) {
      const sectionElements = this.$refs.section
      if (sectionElements && sectionElements.length > sectionIndex) {
        sectionElements[sectionIndex].resetScroll()
      }
    },

    setFocus (event) {
      if (event && event.target.classList.contains('cat-builder__node')) return // node clicked, handled by own event listener
      const nodeElem = document.getElementById(this.displayedNode.guid)
      if (!nodeElem) return
      nodeElem.focus()
    },

    slideSections () {
      const triggerPos = (this.slidePosition + this.visibleSections) - 1
      if (this.sectionClicked === triggerPos && this.sectionsInTree > (triggerPos + 1)) {
        this.slidePosition += 1
        return
      }
      this.slidePosition = Math.max(this.sectionsInTree - this.visibleSections, 0)
    },

    async selectNodeWithPosition (section, index) {
      const safeSection = Math.min(Math.max(section, 0), this.sectionsInTree - 1)
      const treeSection = this.treeSections[safeSection]
      const safeIndex = Math.min(Math.max(index, 0), treeSection.categories.length - 1)
      const hasChangedEffectively = this.displayedNodePosition.section !== safeSection || this.displayedNodePosition.index !== safeIndex
      const isRootNode = this.displayedNodePosition.section === 0 && this.displayedNodePosition.index === 0
      if (hasChangedEffectively || isRootNode) {
        await this.nodeSelected({
          cat: treeSection.categories[safeIndex],
          parentGuid: treeSection.guid
        })
        this.setFocus()
      }
    },

    async selectNodeWithGuid (guid) {
      sectionLoop: for (let i = 0; i < this.treeSections.length; i++) { // eslint-disable-line no-labels
        const section = this.treeSections[i]
        for (let y = 0; y < section.categories.length; y++) {
          const category = section.categories[y]
          if (category.guid === guid) {
            await this.nodeSelected({
              cat: category,
              parentGuid: section.guid
            })
            break sectionLoop // eslint-disable-line no-labels
          }
        }
      }
    },

    setupScrollHandler () {
      this.$refs.scroller.addEventListener('mousedown', event => {
        this.scrollInit.scrollerPos = this.scrollerPosition
        this.scrollInit.mouseX = event.clientX
        this.scrollInit.slidePos = this.slidePosition
        this.scrollMove = this.scrollerPosition
        this.scrolling = true
        window.addEventListener('mousemove', this.scrollMoveHandler)
      })
      window.addEventListener('mouseup', event => {
        this.scrolling = false
        window.removeEventListener('mousemove', this.scrollMoveHandler)
        window.onmousemove = null
      })
    },

    scrollMoveHandler (event) {
      event.preventDefault()
      const deltaX = event.clientX - this.scrollInit.mouseX
      this.scrollMove = Math.min(Math.max(deltaX + this.scrollInit.scrollerPos, 0), this.scrollerWhiteSpace)
      const pages = Math.round((this.scrollMove - this.scrollInit.scrollerPos) / this.scrollerPageSize)
      this.slidePosition = this.scrollInit.slidePos + pages
    },

    keydownHandler (e) {
      let sectionIndex = this.displayedNodePosition.section
      let index = this.displayedNodePosition.index
      const createVisibleInSection = this.$refs.section.findIndex(x => x.createVisible)
      switch (e.key) {
        case 'ArrowLeft':
          if (sectionIndex === 0) break
          sectionIndex -= 1
          index = this.treeSections[sectionIndex].categories.findIndex(x => x.selected)
          break
        case 'ArrowUp':
          index -= 1
          break
        case 'ArrowRight':
          if (this.treeSections[sectionIndex + 1].categories.length) {
            sectionIndex += 1
          }
          break
        case 'ArrowDown':
          index += 1
          break
        case 'c':
          if (createVisibleInSection > -1) return
          this.showCreateInSection(sectionIndex)
          break
        default:
          return
      }
      e.preventDefault()
      this.selectNodeWithPosition(sectionIndex, index)
    },

    moveCreateWidget (e) {
      const createVisibleInSection = this.$refs.section.findIndex(x => x.createVisible)
      if (createVisibleInSection === -1) return
      if (e.key === 'ArrowLeft') {
        this.showCreateInSection(Math.max((createVisibleInSection - 1), 0))
      }
      if (e.key === 'ArrowRight') {
        const maxIndex = this.treeSections.length - 1
        this.showCreateInSection(Math.min((createVisibleInSection + 1), maxIndex))
      }
    },

    hideCreate (keepOpen) {
      for (let index = 0; index < this.$refs.section.length; index++) {
        if (index !== keepOpen) {
          this.$refs.section[index].hideCreate()
        }
      }
      this.setFocus()
    },

    showCreateInSection (index) {
      this.hideCreate(index)
      this.$refs.section[index].showCreate()
    },

    nodeCreated ({ parentGuid, isCatalog }) {
      if (isCatalog) {
        this.getCatalogs()
        return
      }
      this.selectNodeWithGuid(parentGuid)
    }
  },
  components: {
    StoreSelector,
    TreeSection
  },
  mounted () {
    this.setWidth()
    window.addEventListener('resize', () => {
      this.setWidth()
      this.slideSections()
    })
    this.setupScrollHandler()
  }
}
</script>

<style lang="stylus" scoped>
.cat-builder
  box-shadow 0 4px 6px #eee
  position relative
  background-color #fff
  overflow hidden
  min-height 200px
.cat-builder__inner
  white-space nowrap
  transition transform 300ms $easeInOutCirc
  transform translate3d(0, 0, 0)
.cat-builder__header
  position absolute
  width 100%
  height $header_height
  top 0
  background-color lighten($col_table_header_bg, 30%)
  z-index -1
.builder__scrollbar
  height 13px
  border-top solid 1px $col_light_gray
.builder__scroll-handle
  width 80%
  border-radius 15px
  background-color $col_light_gray
  height 12px
  border solid 1px #fff
  transition all 300ms ease
  cursor pointer
  &.scrolling
    transition none

</style>
