<template>
  <div class="uc-text-input" :class="{ big: size === 'big' }">
    <label class="uc-text-input__label input-label" v-if="label" :for="id" :class="{ error: error }">{{label}}</label>
    <input ref="field" class="uc-text-input__input" type="text" :id="id" :value="value" :disabled="readonly" :placeholder="placeholder"
           :class="{ big: size === 'big' }"
    @input="emitValue"
    @focus="$emit('focus', $event)"
    @blur="$emit('blur', $event)">
  </div>
</template>

<script>
import getID from '../../js/id-generator'
export default {
  name: 'uc-text-input',
  data () {
    return {
      id: null
    }
  },
  props: {
    label: String,
    placeholder: String,
    value: String,
    readonly: Boolean,
    error: String,
    size: {
      type: String,
      default: () => 'small'
    }
  },
  methods: {
    emitValue (event) {
      this.$emit('input', event.target.value)
    },
    focus () {
      this.$refs.field.focus()
    }
  },
  created () {
    this.id = getID()
  }
}
</script>

<style lang="stylus" scoped>
.uc-text-input
  &.big
    width 100%
.uc-text-input__input:disabled
  cursor not-allowed
.error
  color $col_red
input.big
  border none
  background none
  font-size 1.5em
  font-weight 600
  padding-left 0
  width 100%
</style>
