<template>
  <div class="uc-rich-text">
    <label class="input-label" v-if="definition.label" :class="{ error: definition.error }">{{definition.label}}</label>
    <div ref="editor"></div>
    <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"></feedback>
  </div>
</template>

<script>
import Quill from 'quill'
import createSaveCommand from '../../js/create-save-command'
import debounce from 'debounce'
import feedback from '../widgets/field-feedback'

export default {
  name: 'uc-rich-text',
  data () {
    return {
      editor: null
    }
  },
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    theme: {
      type: String,
      default: 'snow'
    },
    readonly: Boolean,
    placeholder: {
      type: String,
      default: 'Enter text...'
    },
    label: String
  },
  components: {
    feedback
  },
  watch: {
    'definition.value': function () {
      const handler = this.update
      if (this.definition.value === this.editor.root.innerHTML) {
        return
      }
      this.editor.off('text-change', handler)
      this.editor.root.innerHTML = this.definition.value
      this.$nextTick(function () {
        this.editor.on('text-change', handler)
      })
    }
  },
  mounted () {
    this.editor = new Quill(this.$refs.editor, {
      theme: this.theme,
      readOnly: this.readonly,
      placeholder: this.placeholder
    })
  },
  methods: {
    update: debounce(function () {
      const value = this.editor.root.innerHTML
      if (value === this.definition.value) {
        return
      }
      const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, value, this.definition.value)
      this.$emit('change', saveCommand)
    }, 500)
  }
}
</script>

<style lang="stylus">
@import "../../stylus/_vendor/quill/snow.styl"
@import "../../stylus/_vendor/quill/bubble.styl"
.error
  color $col_red
.ql-editor
  padding 12px 6px
  &.ql-blank::before
    left 6px
</style>
