<template>
  <div class="builder__create" :class="{ toggled: inputVisible }" @click.stop="">
    <a class="builder__create__link" v-show="!inputVisible" href="#" @click.prevent="showInput">{{ addLabel }}</a>
    <form class="builder__create__form" v-show="inputVisible" @submit.prevent="createNewNode">
      <uc-text ref="input" :placeholder="placeholder" @input="updateName" :value="newName" />
      <span class="builder__create__error" v-show="errorMessage.length">{{errorMessage}}</span>
      <div>
        <uc-button :text="'Add'" :type="'submit'" :primary="true" />
        <uc-button class="builder__create__cancel" :text="'Cancel'" :primary="false" @click="hideInput" />
      </div>
    </form>
  </div>
</template>

<script>
import { nodeTypeApiMap, nodeTypeSingular, catalog } from '../../js/tree-node-types'
import UcText from '../../../shared/components/forms/uc-text-input'
import UcButton from '../../../shared/components/forms/uc-button'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters } = createNamespacedHelpers('builder')
export default {
  name: 'create-node',
  data () {
    return {
      inputVisible: false,
      newName: '',
      errorMessage: ''
    }
  },
  props: {
    nodeType: String,
    parentInfo: Object
  },
  components: {
    UcText,
    UcButton
  },
  computed: {
    ...mapGetters(['selectedStore']),
    addLabel () {
      return `Add new ${nodeTypeSingular[this.nodeType]} (c)`
    },
    placeholder () {
      return `C${nodeTypeSingular[this.nodeType].substr(1)} name`
    }
  },
  methods: {
    showInput () {
      this.inputVisible = true
      this.$nextTick()
        .then(() => {
          this.$refs.input.focus()
        })
    },
    hideInput () {
      this.inputVisible = false
      this.errorMessage = ''
    },
    updateName (name) {
      this.newName = name
      this.errorMessage = ''
    },
    createNewNode () {
      if (!this.newName.trim().length) return
      const payload = {
        ...this.parentInfo,
        sortOrder: 0,
        name: this.newName
      }
      if (this.nodeType === catalog) {
        payload.storeGuid = this.selectedStore
      }

      this.axios.post(`${nodeTypeApiMap[this.nodeType]}`, payload)
        .then(res => {
          this.newName = ''
          this.$emit('nodeCreated', { isCatalog: this.nodeType === catalog, parentGuid: this.parentInfo.parentGuid })
          this.$refs.input.focus()
        })
        .catch(err => {
          if (err.response.status === 400) {
            this.errorMessage = err.response.data.Message
          }
        })
    }
  }
}
</script>

<style lang="stylus">
  .builder__create
    position absolute
    z-index 10
    bottom 0
    padding 7px 10px 9px
    width 100%
    background-color rgba(255,255,255,0.9)
    transition all 150ms ease-in
    box-shadow 0 -2px 5px rgba(0,0,0,0.25)
    &.toggled
      padding 11px 10px
      background-color rgba(255,255,255,1)
  .builder__create__link
    color $col_medium_gray
    font-size 0.75rem
    text-decoration none
  .builder__create__form
    box(100%, auto)
    display flex
    flex-direction column
    justify-content space-between
    align-items flex-start
    .uc-text-input, .uc-text-input__input
      width 100%
    .uc-text-input
      margin-bottom 11px
  .builder__create__cancel
    margin-left 15px
  .builder__create__error
   color $col_red
   font-size 0.75rem
   white-space normal
   line-height 12px
   padding 0 0 11px 0

</style>
