<template>
  <div class="uc-single-select" v-if="definition.options.length">
    <label class="uc-input-label" :for="fieldId">{{definition.label}}</label>
    <div class="uc-input-wrapper" v-if="definition.options.length < 6">
      <uc-radio class="uc-single-select__radio" v-for="option in definition.options" :readonly="definition.readonly"
                :key="option.guid" :label="option.label" :guid="option.guid" :checked="option.guid === selectedOption"
                :group="fieldId" @change="change"/>
    </div>
    <template v-else>
      <uc-select :readonly="definition.readonly" :options="definition.options" :selected="selectedOption"
                 :placeholder="definition.placeholder" @selected="change"/>
    </template>
    <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"></feedback>
  </div>
</template>

<script>
import UcRadio from '../forms/uc-radio'
import UcSelect from '../forms/uc-select'
import GetId from '../../js/id-generator.js'
import feedback from '../widgets/field-feedback'
import createSaveCommand from '../../js/create-save-command'

export default {
  name: 'definition-type-picker',
  data () {
    return {
      fieldId: ''
    }
  },
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {
          options: []
        }
      }
    }
  },
  computed: {
    selectedOption () {
      return this.definition.selected ? this.definition.selected[0] : ''
    }
  },
  components: {
    UcRadio,
    UcSelect,
    feedback
  },
  methods: {
    change (item) {
      const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, [item], this.definition.selected, 'selected')
      this.$emit('change', saveCommand)
    }
  },
  created () {
    this.fieldId = GetId()
  }
}
</script>

<style lang="stylus">
  .uc-single-select__radio
    margin-bottom 7px
</style>
