<template>
  <section v-if="details" class="details-view">
    <header class="details-view__header">
      <short-text :definition="details.name" name="name" @change="saveChange" size="big" class="large name field" />
      <div class="details-view__header-right">
        <uc-select-simple :options="languages" :selected="contentLanguage" @selected="changeLanguage"></uc-select-simple>
        <info-widget ref="infoWidget">
          <div class="info widget group">
            <label class="input-label">Created</label>
            <span>{{meta.createdOn | format('DD. MMM YYYY', locale)}}</span>
            by
            <span>{{meta.createdBy}}</span>
            <label class="input-label">Modified</label>
            <span>{{meta.modifiedOn | format('DD. MMM YYYY', locale)}}</span>
            by
            <span>{{meta.modifiedBy}}</span>
          </div>
          <div class="info widget group">
            <label class="input-label">Definition</label>
            <uc-select-simple :options="allDefinitions" :selected="selectedDefinition.guid" @selected="selectDefinition" ref="definitions"></uc-select-simple>
          </div>
          <div class="info widget group">
            <label class="input-label">Careful!</label>
            <div class="details-view__delete" :class="{confirmToggled}">
              <span class="" @click="toggleConfirm">Delete</span>
              <transition name="popover-right">
                <div v-show="confirmToggled" class="dropdown-overlay details__delete-overlay">
                  <button class="btn danger details__delete-btn" @click="deleteItem">Yes, delete this</button>
                  <button class="btn" @click="toggleConfirm">Cancel</button>
                </div>
              </transition>
            </div>
          </div>
        </info-widget>
      </div>
    </header>
    <component :is="displayedNode.nodeType" :language="contentLanguage" @change="saveChange"></component>
    <div class="details__static">
      <div v-for="property in dynamicProperties" :key="details[property].guid">
        <a :name="property"></a>
        <component
            :is="details[property].editor" :language="contentLanguage" :name="property"
             :definition="details[property]" @change="saveChange"></component>
      </div>
    </div>
    <form-feedback type="warning" v-if="anyValidationErrors" class="validation feedback">
      <h5>Please take another look at the content of these fields:</h5>
      <ul>
        <!-- eslint-disable vue/valid-next-line vue/no-unused-vars -->
        <li v-for="(messages, name, index) in validationErrors" :key="`anchor_${name}`">
          <a :href="`#${name}`">{{label(name)}}</a>
          <ul v-for="message in messages" :key="message">
            <li>{{message}}</li>
          </ul>
        </li>
      </ul>
    </form-feedback>
  </section>
</template>

<script>
import { nodeTypeApiMap, nodeTypeDefinitionGuidMap, catalog, category } from '../../js/tree-node-types'
import CatalogDetails from './catalog-details.vue'
import CategoryDetails from './category-details.vue'
import InfoWidget from './info-widget.vue'
import { createNamespacedHelpers, mapGetters, mapMutations, mapActions } from 'vuex'
import Boolean from '../../../shared/components/editors/uc-boolean'
import Date from '../../../shared/components/editors/uc-datepicker'
import DateTime from '../../../shared/components/editors/uc-datetimepicker'
import EnumMultiSelect from '../../../shared/components/editors/uc-multi-select'
import RichText from '../../../shared/components/editors/uc-rich-text'
import ShortText from '../../../shared/components/editors/uc-short-text'
import DefinitionPicker from '../../../shared/components/editors/uc-definition-picker'
import DefinitionTypePicker from '../../../shared/components/editors/uc-definition-type-picker'
import Enum from '../../../shared/components/editors/uc-single-select'
import UcSelectSimple from '../../../shared/components/forms/uc-select-simple'
import Number from '../../../shared/components/editors/uc-number'
import LongText from '../../../shared/components/editors/uc-long-text'
import Media from '../../../shared/components/editors/uc-media'
import ImagePickerMultiSelect from '../../../shared/components/editors/uc-image-picker-multi-select'
import Content from '../../../shared/components/editors/uc-content'
import ContentPickerMultiSelect from '../../../shared/components/editors/uc-content-picker-multi-select'
import EmailContent from '../../../shared/components/editors/uc-email-content'
import createSaveCommand from '../../../shared/js/create-save-command'
import FormFeedback from '../../../shared/components/widgets/form-feedback'
const detailsStore = createNamespacedHelpers('details')
const builderStore = createNamespacedHelpers('builder')

export default {
  name: 'details-section',
  data () {
    return {
      loadDetailsLocked: false,
      bufferedDetailsGuid: null,
      currentDetailsGuid: null,
      typeNameMap: {
        productCategory: 'Category',
        productCatalog: 'Catalog'
      },
      typeDefinitionsMap: {
        productCategory: () => this.categoryDefinitions,
        productCatalog: () => this.catalogDefinitions
      },
      confirmToggled: false
    }
  },
  computed: {
    ...detailsStore.mapGetters(['details', 'categoryDefinitions', 'catalogDefinitions', 'validationErrors']),
    ...builderStore.mapGetters(['displayedNode', 'dragInfo']),
    ...mapGetters(['systemInfo', 'languages', 'locale', 'contentLanguage']),
    meta () {
      return this.details.meta ? this.details.meta : {}
    },
    name () {
      return this.details.name ? this.details.name.value : ''
    },
    selectedDefinition () {
      return this.meta.definition ? this.meta.definition : {}
    },
    allDefinitions () {
      if (this.displayedNode.nodeType) {
        return this.typeDefinitionsMap[this.displayedNode.nodeType]()
      } else {
        return []
      }
    },
    anyValidationErrors () {
      return Object.getOwnPropertyNames(this.validationErrors).filter(n => n !== '__ob__').length > 0
    },
    dynamicProperties () {
      const detailsComponent = this.displayedNode.nodeType === 'productCategory' ? CategoryDetails : CatalogDetails
      const consumed = detailsComponent.consumedProperties + ['meta', 'sortOrder', 'name']
      return Object.keys(this.details).filter(propertyName => !consumed.includes(propertyName))
    }
  },
  watch: {
    async displayedNode () {
      if (this.dragInfo.dragging) {
        return
      }
      if (!this.displayedNode.guid) {
        this.setDetails(undefined)
        return
      }
      this.bufferedDetailsGuid = this.displayedNode.guid
      await this.getDetails()
    }
  },
  components: {
    FormFeedback,
    productCatalog: CatalogDetails,
    productCategory: CategoryDetails,
    UcSelectSimple,
    InfoWidget,
    Boolean,
    Date,
    DateTime,
    Enum,
    EnumMultiSelect,
    RichText,
    ShortText,
    Number,
    LongText,
    DefinitionPicker,
    DefinitionTypePicker,
    Media,
    ImagePickerMultiSelect,
    Content,
    ContentPickerMultiSelect,
    EmailContent
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails', 'setCatalogDefinitions', 'setCategoryDefinitions', 'setPriceGroups', 'updateDetails']),
    ...detailsStore.mapActions(['changeDefinition']),
    ...mapActions(['save', 'delete']),
    ...mapMutations(['setContentLanguage', 'addUndo', 'clearUndos']),
    getDetailsBase () {
      return this.axios.get(`${nodeTypeApiMap[this.displayedNode.nodeType]}/${this.displayedNode.guid}/${this.contentLanguage}`)
    },
    getPriceGroups () {
      if (this.displayedNode.nodeType === 'productCategory') {
        return new Promise((resolve) => {
          resolve({ data: [] })
        })
      }
      return this.axios.get(`catalogs/pricegroups/${this.displayedNode.guid}`)
    },
    async getDetails () {
      if (!this.loadDetailsLocked) {
        this.loadDetailsLocked = true
        this.currentDetailsGuid = this.displayedNode.guid
        const promises = [this.getDetailsBase(), this.getPriceGroups()]
        const [details, priceGroups] = await Promise.all(promises)
        this.populateDetails(details, priceGroups)
      }
    },
    label (propertyName) {
      return this.details[propertyName].label
    },
    populateDetails (details, priceGroups) {
      this.loadDetailsLocked = false
      if (this.currentDetailsGuid !== this.bufferedDetailsGuid) {
        this.getDetails()
        return
      }
      this.setDetails(details.data)
      this.setPriceGroups(priceGroups.data)
    },
    saveChange (command) {
      this.enrichCommand(command)
      this.save(command)
      this.$emit('change', command)
    },
    enrichCommand (command) {
      const { meta } = command
      meta.contentLanguage = this.contentLanguage
      meta.nodeType = this.displayedNode.nodeType
      meta.nodeGuid = this.displayedNode.guid
    },
    toggleConfirm (event) {
      event.preventDefault()
      this.confirmToggled = !this.confirmToggled
      if (this.confirmToggled) {
        window.setTimeout(() => {
          window.addEventListener('click', this.closeHandler)
        }, 10)
      }
    },
    async deleteItem () {
      this.confirmToggled = false
      const command = createSaveCommand('deleted', 'cbaf8c70-639c-4716-a891-00efb0fb82bc', 'Is Deleted', true, false)
      const { meta } = command
      meta.contentLanguage = this.contentLanguage
      meta.nodeType = this.displayedNode.nodeType
      meta.nodeGuid = this.displayedNode.guid
      meta.survivesNodeChange = true
      meta.undoMessage = `${this.details.name.value} was deleted`
      meta.undoAction = () => { this.$emit('resurrected', this.displayedNode.guid) }
      await this.delete(command)
      await this.$emit('deleted')
      this.$refs.infoWidget.hide()
    },
    closeHandler () {
      window.removeEventListener('click', this.closeHandler)
      this.confirmToggled = false
    },
    changeLanguage (language) {
      this.bufferedDetailsGuid = this.displayedNode.guid
      this.setContentLanguage(language)
      this.getDetails()
    },
    selectDefinition (definitionGuid) {
      const oldValue = this.details.meta.definition ? this.details.meta.definition.guid : null
      const command = createSaveCommand('definition', nodeTypeDefinitionGuidMap[this.displayedNode.nodeType], 'definition', definitionGuid, oldValue)
      this.enrichCommand(command)
      this.changeDefinition(command)
      this.$refs.definitions.hide()
      this.$refs.infoWidget.hide()
    }
  },
  async created () {
    const catalogDefinitions = await this.axios.get(`${nodeTypeApiMap[catalog]}/definitions`)
    const categoryDefinitions = await this.axios.get(`${nodeTypeApiMap[category]}/definitions`)
    this.setCatalogDefinitions(catalogDefinitions.data)
    this.setCategoryDefinitions(categoryDefinitions.data)
  }
}
</script>

<style lang="stylus" scoped>
  .details-view
    margin-bottom 60px
  .details-view__header
    border-bottom 1px solid $col_light_gray
    margin 0 20px
    display flex
    justify-content space-between
    align-items center
    min-height 70px
    .large.name.field
      flex 1
  .details__type-label
    display block
    font-weight 600
    font-size 0.5625rem
  .details-view__header-right
    display grid
    grid-template-columns auto auto
    grid-column-gap 20px
    align-items baseline
    justify-items end
  .details-view__delete
    position relative
    display inline-block
    font-size 0.8125rem
    color $col_red
    cursor pointer
    border-radius 4px
    transition background-color 200ms
    &.confirmToggled
      background-color lighten($col_red, 85%)
  .details__delete-overlay
    transform-origin top right
    display flex
    right 0
    left auto !important
    margin-top 3px
  .details__static
    padding 35px 20px 20px 20px
    display grid
    grid-template-columns 1fr
    align-items center
    grid-row-gap 36px
  .info.widget.group
    margin-bottom 20px
  .validation.feedback
    font-size 1rem
    opacity 0.85
    color $col_black
    a,a:hover,a:active,a:visited
      color $col_black
    ul > li
      margin-bottom 8px
    ul > li > ul
      list-style none
      padding-left 0
</style>
