<template>
  <div class="details-category">
    <section class="details-category__static">
      <uc-image-picker :definition="details.media" name="media" :multi-select="false" content-type="media" @change="save"/>
      <div class="details-category__static-right">
        <uc-short-text :showLanguage="true" :language="language" :definition="details.displayName" name="displayName" @change="save" />
        <uc-rich-text :definition="details.description" name="description" @change="save"></uc-rich-text>
        <uc-boolean :definition="details.displayOnWebsite" name="displayOnWebsite" @change="save" />
        <uc-single-select :definition="details.definition" name="definition" @change="save" />
      </div>
    </section>
  </div>
</template>

<script>
import UcImagePicker from '../../../shared/components/editors/uc-image-picker'
import UcShortText from '../../../shared/components/editors/uc-short-text'
import UcRichText from '../../../shared/components/editors/uc-rich-text'
import UcBoolean from '../../../shared/components/editors/uc-boolean'
import UcSingleSelect from '../../../shared/components/editors/uc-single-select'
import { createNamespacedHelpers, mapGetters as globalGetters } from 'vuex'
const { mapGetters } = createNamespacedHelpers('details')
export default {
  name: 'category-details',
  props: {
    language: String
  },
  computed: {
    ...mapGetters(['details', 'priceGroups', 'categoryDefinitions']),
    ...globalGetters(['systemInfo'])
  },
  consumedProperties: ['displayName', 'name', 'description', 'displayOnWebsite'],
  components: {
    UcImagePicker,
    UcShortText,
    UcRichText,
    UcBoolean,
    UcSingleSelect
  },
  methods: {
    save (e) {
      this.$emit('change', e)
    }
  }
}
</script>

<style lang="stylus">
.details-category__static
  padding 35px 20px 20px 20px
  display flex
  align-items flex-start
  justify-content space-between
.details-category__static-right
  margin-left 40px
  display grid
  grid-row-gap 36px
  flex-grow 1

</style>
