<template>
  <div class="preview-bar wrapper">
    <div class="preview-bar container">
      <label>{{ selectedItemsSummary }}</label>
      <button class="preview-bar save btn primary" @click="save">Save</button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'content-selector-preview-bar',
  computed: {
    ...mapGetters('navigator', ['selectedItems']),
    selectedItemsSummary () {
      const count = this.selectedItems.length
      switch (count) {
        case 0: return 'Nothing selected'
        case 1: return `1 item selected`
        default: return `${count} items selected`
      }
    }
  },
  methods: {
    save () {
      this.$emit('save', this.selectedItems)
    }
  }
}
</script>

<style lang="stylus" scoped>
.preview-bar
  position relative
  &.wrapper
    width 100%
    height 60px
    background-color #ffffff
  &.container
    width 100%
    height 100%
    border-top solid 1px $col_light_gray
    center()
  &.save
    height 100%
    position absolute
    right 0
</style>
