<template>
  <media :definition="definition" :name="name" :multi-select="false" :language="language" content-type="media" @change="change"></media>
</template>

<script>
import Media from './uc-image-picker'
export default {
  name: 'uc-media',
  components: {
    Media
  },
  props: {
    name: String,
    definition: Object,
    language: String
  },
  methods: {
    change (command) {
      this.$emit('change', command)
    }
  }
}
</script>

