<template>
  <label class="uc-radio" :class="{readonly}">
    <input type="radio" :name="group" :id="id" :disabled="readonly" :value="guid" :checked="checked" @change="onChange">
    <span>{{label}}</span>
    <div class="uc-radio__check">
      <div class="uc-radio__check-inner"></div>
    </div>
  </label>
</template>

<script>
import GetId from '../../js/id-generator.js'
export default {
  name: 'uc-radio',
  data () {
    return {
      id: null
    }
  },
  props: {
    label: String,
    readonly: Boolean,
    checked: Boolean,
    group: String,
    guid: String
  },
  methods: {
    onChange (event) {
      this.$emit('change', this.guid)
    }
  },
  created () {
    this.id = GetId()
  }
}
</script>

<style lang="stylus">
.uc-radio
  cursor pointer
  position: relative
  padding-left: 34px
  display flex
  align-items center
  font-size $input_font_size
  font-weight 600
  &.readonly
    cursor not-allowed
  input[type=radio]
    position: absolute
    visibility: hidden
  input[type=radio]:checked
    & ~ .uc-radio__check
      border-color $col_dark_selection
      .uc-radio__check-inner
        background-color: $col_dark_selection
        circle(10px)

.uc-radio__check
  position: absolute
  left 0
  top 0
  circle(16px)
  border solid 2px $col_dark_selection
  background-color: #fff
  display: flex
  transition: border-color 90ms linear
  .readonly &
    opacity .6
.uc-radio__check-inner
  circle(2px)
  margin auto
  background-color #fff
  transition all 150ms ease-in
</style>
