<template>
  <uc-select class="store-selector" :label="'Stores'" :options="stores" :selected="selectedStore" @selected="setSelectedStore" :placeholder="'Select a store'"/>
</template>

<script>
import UcSelect from '../../../shared/components/forms/uc-select.vue'
import MapStores from '../../js/map-get-stores-response'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapMutations } = createNamespacedHelpers('builder')

export default {
  name: 'store-selector',
  components: {
    UcSelect
  },
  computed: {
    ...mapGetters(['selectedStore', 'stores'])
  },
  methods: {
    ...mapMutations(['setSelectedStore', 'setStores']),
    async getStores () {
      const res = await this.axios.get('/stores')
      const mappedStores = MapStores(res.data)
      this.setStores(mappedStores)
      if (!this.stores.length) return
      this.setSelectedStore(this.stores[0])
    }
  },
  created () {
    this.getStores()
  }
}
</script>

<style lang="stylus">
.store-selector
  margin-bottom 12px
</style>
