<template>
  <div class="uc-text-area">
    <label class="uc-text-area__label input-label" v-if="label" :for="id" :class="{ error: error }">{{label}}</label>
    <textarea class="uc-text-area__area" rows="5" :id="id" :value="value" :disabled="readonly" :placeholder="placeholder"
    @input="emitValue"
    @focus="$emit('focus', $event)"
    @blur="$emit('blur', $event)"></textarea>
  </div>
</template>

<script>
import getID from '../../js/id-generator'
export default {
  name: 'uc-text-area',
  data () {
    return {
      id: null
    }
  },
  props: {
    label: String,
    placeholder: String,
    value: String,
    readonly: Boolean,
    error: String
  },
  methods: {
    emitValue (event) {
      this.$emit('input', event.target.value)
    },
    focus () {
      this.$refs.field.focus()
    }
  },
  created () {
    this.id = getID()
  }
}
</script>

<style lang="stylus">
.uc-text-area__area
  input-field()
  max-width 500px
  width 100%
  padding 5px 2px
  line-height 1.6
  &:disabled
    cursor not-allowed
.error
  color $col_red
</style>
