<template>
  <div class="details-catalog">
    <section class="details__static">
      <uc-short-text :showLanguage="true" :language="language" :definition="details.displayName" :name="'displayName'" @change="save" />
      <uc-boolean :definition="details.displayOnWebsite" :name="'displayOnWebsite'" @change="save" />
      <uc-boolean :definition="details.showPricesIncludingVAT" :name="'showPricesIncludingVAT'" @change="save" />
      <div></div>
      <uc-single-select :definition="details.defaultPriceGroup" :name="'defaultPriceGroup'" @change="save" />
      <uc-multi-select :definition="details.allowedPriceGroups" :name="'allowedPriceGroups'" @change="save" />
    </section>
  </div>
</template>

<script>
import UcSingleSelect from '../../../shared/components/editors/uc-single-select'
import UcMultiSelect from '../../../shared/components/editors/uc-multi-select'
import UcShortText from '../../../shared/components/editors/uc-short-text'
import UcBoolean from '../../../shared/components/editors/uc-boolean'
import { createNamespacedHelpers, mapGetters as globalGetters } from 'vuex'
const { mapGetters } = createNamespacedHelpers('details')
export default {
  name: 'catalog-details',
  props: {
    language: String
  },
  computed: {
    ...mapGetters(['details']),
    ...globalGetters(['systemInfo'])
  },
  consumedProperties: ['displayName', 'displayOnWebsite', 'showPricesIncludingVAT', 'defaultPriceGroup', 'allowedPriceGroups'],
  components: {
    UcSingleSelect,
    UcMultiSelect,
    UcShortText,
    UcBoolean
  },
  methods: {
    save (e) {
      this.$emit('change', e)
    }
  }
}
</script>

<style lang="stylus" scoped>
.details__static
  padding 35px 20px 20px 20px
  display grid
  grid-template-columns 1fr 1fr 1fr
  align-items start
  grid-row-gap 36px
.details-catalog__pricegroups
  margin-top 16px
.details-catalog__pricegroup-check
  margin-top 10px
</style>
