<template>
  <div class="catalogs" :class="{dragging: dragging}">
    <header class="catalogs__header">
      <h1>Catalogs</h1>
    </header>
    <section class="catalogs__builder-outer">
      <cat-builder ref="builder"></cat-builder>
      <div class="catalogs__builder-resize-outer">
        <div class="catalogs__builder-resize-inner" @mousedown="onMousedown"></div>
      </div>
    </section>
    <details-view @deleted="selectParent" @resurrected="reload" @change="detailsChanged"></details-view>
    <undo :undos="undos" @undo="executeUndo"></undo>
    <form-feedback type="error" v-if="errorMessage">{{errorMessage}}</form-feedback>
  </div>
</template>

<script>
import CatBuilder from '../components/catalog-builder/catalog-builder.vue'
import DetailsView from '../components/details-view/details.vue'
import { createNamespacedHelpers, mapGetters, mapActions } from 'vuex'
import Undo from '../../shared/components/widgets/undo-widget'
import FormFeedback from '../../shared/components/widgets/form-feedback'
const builderStore = createNamespacedHelpers('builder')

export default {
  name: 'catalogs',
  data () {
    return {
      startY: null,
      dragging: false,
      minHeight: 300
    }
  },
  components: {
    FormFeedback,
    CatBuilder,
    DetailsView,
    Undo
  },
  computed: {
    ...builderStore.mapGetters(['sectionHeight', 'displayedNode']),
    ...mapGetters(['undos', 'errorMessage'])
  },

  methods: {
    ...builderStore.mapMutations(['setSectionHeight', 'setCurrentNodeName']),
    ...mapActions(['undo']),
    ...mapActions(['setErrorMessage', 'setNotFoundMessage']),
    onMousedown (event) {
      event.preventDefault()
      this.startY = event.clientY
      this.dragging = true
      window.addEventListener('mousemove', this.onMousemove)
    },

    onMousemove (event) {
      let delta = event.clientY - this.startY
      this.startY = event.clientY
      if (this.sectionHeight < this.minHeight && delta < 0) return
      this.setSectionHeight(this.sectionHeight + delta)
    },

    onMouseup () {
      this.dragging = false
      window.removeEventListener('mousemove', this.onMousemove)
      window.onmousemove = null
    },

    async selectParent () {
      await this.$refs.builder.selectParent({ clearUndo: false })
    },

    detailsChanged (command) {
      if (command.save['name']) {
        this.setCurrentNodeName(command.save['name'].value)
      }
    },

    executeUndo (command) {
      this.undo(command)
      if (command.undo['name']) {
        this.setCurrentNodeName(command.undo['name'].value)
      }
    },

    reload (nodeGuid) {
      const builder = this.$refs.builder
      builder.selectNodeWithGuid(nodeGuid)
    }
  },
  mounted () {
    window.addEventListener('mouseup', this.onMouseup)
    this.axios.interceptors.response.use(response => response, error => {
      if (error.response.status === 500) {
        this.setErrorMessage(error.message)
      } else if (error.response.status === 404) {
        this.setNotFoundMessage()
      }
      return Promise.reject(error)
    })
  }
}
</script>
<style lang="stylus">
.catalogs
  &.dragging
    cursor n-resize

.catalogs__builder-outer
  padding 40px 20px

.catalogs__header
  height 100px
  background-color $col_page_header_bg
  border-bottom $page_header_border
  padding 1px 20px
  display flex
  align-items center
  h1
    margin 0
    font-size 1.125rem
    font-weight 600
    text-transform uppercase
    &:after
      content "BETA"
      color $col_primary
      font-weight 100
      padding-left 10px
.catalogs__builder-resize-outer
  display flex
  flex-direction column

.catalogs__builder-resize-inner
  align-self center
  width 20px
  height 6px
  margin-top: 6px
  border-top $page_header_border
  border-bottom $page_header_border
  cursor n-resize

</style>
