<template>
  <label class="uc-checkbox" :class="{readonly}">
    <input type="checkbox" :name="id" :id="id" :disabled="readonly" :checked="checkedValue" :value="guid" @change="onChange">
    <span class="uc-checkbox__label">{{label}}</span>
    <div class="uc-checkbox__check">
      <check></check>
    </div>
  </label>
</template>

<script>
import GetId from '../../js/id-generator.js'
import Check from '../../icons/check.vue'
export default {
  name: 'uc-checkbox',
  data () {
    return {
      id: null
    }
  },
  props: {
    label: String,
    readonly: Boolean,
    checked: Boolean,
    guid: String
  },
  components: {
    Check
  },
  computed: {
    checkedValue () {
      return this.checked ? 'checked' : ''
    }
  },
  methods: {
    onChange (event) {
      this.$emit('change', { value: event.target.checked, guid: this.guid })
    }
  },
  created () {
    this.id = GetId()
  }
}
</script>

<style lang="stylus">
.uc-checkbox
  cursor pointer
  position: relative
  padding-left: 34px
  display flex
  align-items center
  font-size $input_font_size
  font-weight 600
  &.readonly
    cursor not-allowed
  input[type=checkbox]
    position: absolute
    visibility: hidden
  input[type=checkbox]:checked
    & ~ .uc-checkbox__check
      svg
        stroke-dashoffset 0
      border-color $col_dark_selection
      background-color: $col_dark_selection

.uc-checkbox__check
  position: absolute
  left 0
  top 0
  box(16px)
  border-radius: 2px
  border solid 2px $col_dark_selection
  background-color: #fff
  display: flex;
  transition: border-color 90ms linear
  svg
    stroke #fff
    stroke-width 1
    box(90%)
    stroke-dasharray 30
    stroke-dashoffset 30
    margin: auto
    transition: stroke-dashoffset 200ms linear
  .readonly &
    opacity .6

</style>
