<template>
  <div class="picker-image" @mouseover="hovered = true" @mouseleave="hovered = false">
    <uc-image class="absolute" :src="item.url" :alt="item.name" :size="size"/>
    <div v-show="hovered" class="absolute border" :style="{ width: size, height: size}"/>
    <div v-show="selected" class="absolute background" :style="{ width: size, height: size}"/>
    <div v-show="selected" class="absolute badge label-container" :style="{ left: offsetPosition }">{{ selectedItemPosition }}</div>
    <div v-show="nameShown" class="absolute name label-container" :style="{ top: offsetName }">{{ item.name }}</div>
  </div>
</template>

<script>
import UcImage from '../../../shared/components/uc-image'
import { mapGetters } from 'vuex'

export default {
  name: 'picker-image',
  data () {
    return {
      fallback: false,
      hovered: false
    }
  },
  props: {
    item: Object,
    sizeObj: {
      value: Number,
      suffix: String
    },
    selected: Boolean,
    showName: {
      type: Boolean,
      default: false
    }
  },
  components: {
    UcImage
  },
  computed: {
    ...mapGetters('navigator', ['selectedItems']),
    size () {
      return `${this.sizeObj.value}${this.sizeObj.suffix}`
    },
    selectedItemPosition () {
      return this.selectedItems.indexOf(this.item.guid) + 1
    },
    nameShown () {
      return this.showName || this.selected || this.hovered
    },
    offsetPosition () {
      return this.getSize(this.sizeObj.value - 44)
    },
    offsetName () {
      return this.getSize(this.sizeObj.value - 36)
    }
  },
  methods: {
    getSize (value) {
      return `${value}${this.sizeObj.suffix}`
    }
  }
}
</script>

<style lang="stylus" scoped>
.picker-image
  position relative
.absolute
  position absolute
.border
  border 2px solid $col_dark_selection
.background
  background-color $col_dark_selection
  opacity 0.2

.label-container
  background-color #ffffff
  opacity .9
  margin 6px
.badge
  box(32px)
  center()
.name
  padding 2px 8px
  width calc(100% - (2 * 6px))
  truncate()
</style>
