const getters = {
  details: state => state.details,
  categoryDefinitions: state => state.categoryDefinitions.map(x => {
    return {
      text: x.name,
      value: x.guid
    }
  }),
  catalogDefinitions: state => state.catalogDefinitions.map(x => {
    return {
      text: x.name,
      value: x.guid
    }
  }),
  priceGroups: state => state.priceGroups.map(x => {
    return {
      text: x.name,
      value: x.guid
    }
  }),
  validationErrors: state => state.validationErrors
}

export default getters
