<template>
  <div class="uc-boolean-select">
    <uc-checkbox :readonly="definition.readonly" :label="definition.label" :guid="definition.guid" :checked="definition.value" @change="change" />
    <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"></feedback>
  </div>
</template>

<script>
import feedback from '../widgets/field-feedback'
import UcCheckbox from '../forms/uc-checkbox'
import CreateSaveCommand from '../../js/create-save-command'
export default {
  name: 'boolean',
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  components: {
    UcCheckbox,
    feedback
  },
  methods: {
    change (e) {
      const command = CreateSaveCommand(this.name, this.definition.guid, this.definition.label, e.value, this.definition.value)
      this.$emit('change', command)
    }
  }
}
</script>

