<template>
  <div class="uc-datetime-picker">
    <label class="input-label" v-if="definition.label" :class="{ error: definition.error }">{{definition.label}}</label>
    <div class="uc-datetime-picker__group">
      <date-picker
      :value="definition.value"
      :format="formatter"
      :placeholder="definition.placeholder"
      @selected="changeDate"
      />
      <time-picker :value="timeData" @change="changeTime" format="HH:mm"></time-picker>
    </div>
    <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"></feedback>
  </div>
</template>

<script>
import DatePicker from 'vuejs-datepicker'
import TimePicker from 'vue2-timepicker'
import { Format, locales } from '../../js/date-formatter'
import CreateSaveCommand from '../../js/create-save-command'
import feedback from '../widgets/field-feedback'
export default {
  name: 'uc-date-time-picker',
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    format: {
      type: String,
      default: 'DD. MMM. YYYY'
    },
    locale: {
      type: String,
      default: 'en'
    },
    label: String
  },
  computed: {
    timeData () {
      const date = this.getDate()
      if (date) {
        return {
          HH: date.getHours().toString().padStart(2, '0'),
          mm: date.getMinutes().toString().padStart(2, '0')
        }
      } else {
        return { HH: undefined, mm: undefined }
      }
    }
  },
  components: {
    DatePicker,
    TimePicker,
    feedback
  },
  methods: {
    getDate () {
      const date = new Date(this.definition.value)
      if (date instanceof Date && !isNaN(date)) {
        // Truncate date to make comparison possible
        return new Date(date.getFullYear(), date.getMonth(), date.getDate(), date.getHours(), date.getMinutes())
      } else {
        return undefined
      }
    },
    changeTime (time) {
      const date = this.getDate() || new Date()
      if (date) {
        const dateTime = new Date(
          date.getFullYear(), date.getMonth(), date.getDate(), time.data.HH, time.data.mm)
        this.emitIfChanged(dateTime)
      }
    },
    changeDate (newDate) {
      const date = this.getDate() || new Date()
      if (date) {
        const dateTime = new Date(
          newDate.getFullYear(), newDate.getMonth(), newDate.getDate(), date.getHours(), date.getMinutes())
        this.emitIfChanged(dateTime)
      }
    },
    emitIfChanged (dateTime) {
      const previousDate = this.getDate() || new Date()
      if (dateTime.getTime() !== previousDate.getTime()) {
        const command = CreateSaveCommand(this.name, this.definition.guid, this.definition.label, dateTime, previousDate)
        this.$emit('change', command)
      }
    },
    formatter (date) {
      return Format(date, this.format, locales[this.locale])
    }
  }
}
</script>

<style lang="stylus">
.uc-datetime-picker__group
  display flex
.vdp-datepicker
  flex 120px 0 0
.time-picker
  flex 80px 1 0
  input.display-time
    border none
    border-bottom 1px solid $col_light_border
    padding 0 6px
    font-size 0.875rem
    height auto
  .clear-btn
    display none
.error
  color $col_red
</style>
