import * as types from './mutation-types'
import * as generalTypes from './../mutation-types'
import { nodeTypeApiMap } from '../../js/tree-node-types'

const actions = {
  async changeDefinition ({ commit, dispatch }, command) {
    const { meta, save, undo } = command

    // First, send the changed definition guid to the API
    await this._vm.axios.patch(`${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}/${meta.contentLanguage}`, save)

    // Then, reload the details and save the details
    const details = await this._vm.axios.get(`${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}/${meta.contentLanguage}`)

    commit(types.SET_DETAILS, details.data)

    if (undo) {
      meta.undoAction = function () {
        dispatch('changeDefinition', { meta: command.meta, save: command.undo })
      }
      commit(generalTypes.ADD_UNDO, command, { root: true })
      window.setTimeout(() => {
        commit('removeUndo', command, { root: true })
      }, 20000)
    }
  }
}

export default actions
